import { Injectable } from '@angular/core';
import "rxjs/add/operator/map";
import { Subject } from 'rxjs/Subject';
import { PassData } from '../model/passData.model';


@Injectable()
export class DashboardToFormCreationService {

  
  private finalFormId: string;
  private passData: PassData[] = [];

  constructor() { }


  // prenosi listu sa required poljima
  getPassData(){
    return this.passData;
  }

  addPassData(item: PassData){
    this.passData.push(item);
  }

  resetList(){
    this.passData = [];
  }


  // prenosi finalFormId na sledecu stranu
  getfinalFormId(){
    return this.finalFormId;
  }

  addfinalFormId(finalFormId){
    this.finalFormId = finalFormId;
  }




}



