import { Injectable } from '@angular/core';
import { Http, Headers } from "@angular/http";
import 'rxjs/add/operator/map';
import { tokenNotExpired } from 'angular2-jwt';
import { Router } from '@angular/router';



@Injectable()
export class AuthService {

  authToken: any;
  user:any;


  constructor(private http: Http, private router: Router) { }


  registerUser(user){
    let headers = new Headers();
    headers.append('Content-Type', "application/json");
    // return this.http.post('http://localhost:3000/users/register', user, {headers: headers}).map(res => res.json());
    // ovo je za heroku
    return this.http.post('users/register', user, {headers: headers}).map(res => res.json());
  }

 authenticateUser(user){
  let headers = new Headers();
  headers.append('Content-Type', "application/json");
  // return this.http.post('http://localhost:3000/users/authenticate', user, {headers: headers}).map(res => res.json());
  return this.http.post('users/authenticate', user, {headers: headers}).map(res => res.json());
 }


 getProfile(){
  // na backendu je ova strana protekt pa mora da se posalje i token sa hederom
  let headers = new Headers();
  this.loadToken();
  // console.log('ovo je token: ' + this.authToken);
  headers.append('Authorization', this.authToken);
  headers.append('Content-Type', "application/json");
  // return this.http.get('http://localhost:3000/users/profile',{headers: headers}).map(res => res.json());
  return this.http.get('users/profile', {headers: headers}).map(res => res.json());
 }

 loadToken(){
   const token = localStorage.getItem('id_token');
   this.authToken = token;
  //  console.log('ovo je svaki put da se napravi token: ' + this.authToken);
 }

 // proverimo da li smo ulogovani da pokazemo login button ili ne(proverimo da li je token tu i da li je expired)
 loggedIn(){
  return tokenNotExpired('id_token');
 }


 storeUserData(token, user){
   // u localStorage ide save kao key value par
   // u browseru na aplication tab odemo da vidimo vrednosti
  //  id_token zato sto smo na backendu koristili passport koji iz headera trazi token i default je id_token
   localStorage.setItem('id_token',token);
   // localStorage moze da primi samo string pa koristimo JSON.stringify(user) da bi dobili string i onda ponovo iz naslova imamo json kad ga posle koristimo
   localStorage.setItem('user', JSON.stringify(user));
   this.authToken = token;
   this.user = user;
 }

 logout(){
   this.authToken = null;
   this.user = null;
   localStorage.clear();
 }


 submitForm(submitData){
    let headers = new Headers();
    // this.loadToken();
    // console.log('ovo je token: ' + this.authToken);
    // headers.append('Authorization', this.authToken);
    console.log('ovo je token iz formCreation: ' + this.authToken);
    headers.append('Content-Type', "application/json");
    // return this.http.post('http://localhost:3000/form/formCreation', submitData).map(res => res.json());
    // ovo je za heroku
    return this.http.post('form/formCreation', submitData, {headers: headers}).map(res => res.json());
 }

 submitUserCreatedForm(userFormCreated){
  let headers = new Headers();
  this.loadToken();
  // console.log('ovo je token: ' + this.authToken);
  headers.append('Authorization', this.authToken);
  headers.append('Content-Type', "application/json");
  // return this.http.post('http://localhost:3000/form/formCreatedByUser', userFormCreated, {headers: headers}).map(res => res.json());
  return this.http.post('form/formCreatedByUser', userFormCreated, {headers: headers}).map(res => res.json());

}

getFinalForm(){
  let headers = new Headers();
  headers.append('Content-Type', "application/json");
  // ovo ispod smo dobili current url pa smo podelili da bi dobili samo id
  var finalFormId = this.router.url.split('/');
  // console.log(finalFormId[2]);
  // return this.http.get('http://localhost:3000/form/finalForm/' + finalFormId[2]).map(res => res.json());
  return this.http.get('form/finalForm/' + finalFormId[2]).map(res => res.json());
}


getAllFormsForUser(userId){
  let headers = new Headers();
  this.loadToken();
  // console.log('ovo je token: ' + this.authToken);
  headers.append('Authorization', this.authToken);
  headers.append('Content-Type', "application/json");
  var finalFormUserId = localStorage.getItem('user').split(',')[0].split(':')[1].replace('"','').replace('"','');
  console.log('ovo je finalFormUserId: ' + finalFormUserId);
  // return this.http.get('http://localhost:3000/form/finalForm/data/' + finalFormUserId, {headers: headers}).map(res => res.json());
  return this.http.get('form/finalForm/data/' + finalFormUserId, {headers: headers}).map(res => res.json());
}
getAllSubmitetFromsForUser(userId){
  let headers = new Headers();
  this.loadToken();
  // console.log('ovo je token: ' + this.authToken);
  headers.append('Authorization', this.authToken);
  headers.append('Content-Type', "application/json");
  // console.log(finalFormUserId);
  // return this.http.get('http://localhost:3000/form/form/' + userId, {headers: headers}).map(res => res.json());
  return this.http.get('form/form/' + userId, {headers: headers}).map(res => res.json());
}


}



