import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule, Routes } from "@angular/router";

import { AppComponent } from './app.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { LoginComponent } from './components/login/login.component';
import { RegisterComponent } from './components/register/register.component';
import { HomeComponent } from './components/home/home.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { ProfileComponent } from './components/profile/profile.component';
import { ValidateService } from './services/validate.service';
import { FlashMessagesModule } from 'angular2-flash-messages';
import { AuthService } from './services/auth.service';
import { AuthGuard } from './guards/auth.guard';
import { ReactiveFormsModule } from '@angular/forms'
import { NgxQRCodeModule } from "ngx-qrcode2";
import { FormCreationComponent } from "./components/form-creation/form-creation.component";
import { DashboardToFormCreationService } from "./services/dashboard-to-form-creation.service";
import { NgxPhoneMaskModule } from 'ngx-phone-mask';
import { TextMaskModule } from 'angular2-text-mask';
import { FinalFormComponent } from './components/final-form/final-form.component';


const appRoutes: Routes = [
  {path:'', component: HomeComponent},
  {path:'register', component: RegisterComponent},
  {path:'login', component: LoginComponent},
  // {path:'dashboard', component: DashboardComponent, canActivate:[AuthGuard]},
  {path:'dashboard', component: DashboardComponent, canActivate:[AuthGuard]},
  {path:'profile', component: ProfileComponent, canActivate:[AuthGuard]},
  {path:'formCreation', component: FormCreationComponent, canActivate:[AuthGuard]},
  {path:'formCreatedByUser', component: DashboardComponent, canActivate:[AuthGuard]},
  {path:'finalForm/:finalFormId', component: FinalFormComponent, canActivate:[AuthGuard]},
  {path:'form/', component: FinalFormComponent, canActivate:[AuthGuard]},
  {path:'form/:userId', component: FinalFormComponent, canActivate:[AuthGuard]},
  {path: '**', redirectTo: ''}
]

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    LoginComponent,
    RegisterComponent,
    HomeComponent,
    DashboardComponent,
    ProfileComponent,
    FormCreationComponent,
    FinalFormComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(appRoutes),
    FlashMessagesModule,
    ReactiveFormsModule,
    NgxQRCodeModule,
    NgxPhoneMaskModule,
    TextMaskModule

    
  ],
  providers: [ValidateService, AuthService, AuthGuard, DashboardToFormCreationService],
  bootstrap: [AppComponent]
})
export class AppModule { }



