import { Component, OnInit, AfterContentInit } from '@angular/core';
import { AuthService } from "../../services/auth.service";
import { Router } from "@angular/router";
import { FlashMessagesService } from 'angular2-flash-messages/module/flash-messages.service';


@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit{
  createdCode = null;

  private allForms = [];
  private showHtmlList = false;

  private allSubmitedFormsForUser = []
  private showHtmlSubmitedForms = false;

  private user: Object;
  private name:string;
  private userId:string;

  private finalFormObject = {
    finalFormUserId:String,
    finalFormFromId:String,
    finalFormFields:Array
  };


  constructor(
    private authService: AuthService,
    private router: Router,
    private flashMessage: FlashMessagesService
  ) { }

  ngOnInit() {
    this.authService.getProfile().subscribe(profile =>{
      this.user = profile.user;
      this.name = profile.user.username;
      this.userId = profile.user._id;
    },
    
    err => {
      console.log(err);
      return false;
    });
    
  }

  createBarcode(){
    this.createdCode = this.name + ', je covek koji je osmislio Aplikaciju :)';
    // console.log(this.name);
    // console.log(this.userId);
  }  


  listAllFormsForUser(){
    this.authService.getAllFormsForUser(this.userId).subscribe(data =>{
      // console.log(data)
      this.allForms = data;
      // console.log(this.allForms)
      this.showHtmlList = true;
      this.showHtmlSubmitedForms = false;
    },
    err => {
      console.log(err);
      return false;
    });
  }


  closeWindow(){
    this.showHtmlList = false;
  }


  listAllSubmitetFormsForUser(){
    this.authService.getAllSubmitetFromsForUser(this.userId).subscribe(data=>{
      console.log("ovo je data: " + data);
      this.allSubmitedFormsForUser = data;
      // console.log(this.allForms)
      this.showHtmlList = false;
      this.showHtmlSubmitedForms = true;
    },
    err => {
      console.log(err);
      return false;
    });
  }

  closeUserSubmitedFormsWindow(){
    this.showHtmlSubmitedForms = false;
  }

}



